<template>
    <div class="re-drop-menu" ref="dropDown">
        <div class="re-drop-link" @click="showDropDown">
            <slot v-if="hasSlots" name="drop-link"></slot>
            <span v-else>{{text}}</span>
        </div>
        <div class="re-drop-down-list" v-if="isShow">
            <slot name="drop-down"></slot>
        </div>
    </div>
</template>

<script>
export default {
  name: 're-drop-menu',
  data () {
    return {
      isShow: false
    }
  },
  props: {
    text: {
      type: String, default: ''
    },
    width: {
      type: Number, default: 80
    }
  },
  computed: {
    hasSlots () {
      let isExist = false
      for (let key in this.$slots) {
        if (key === 'drop-link') {
          isExist = true
        }
      }
      return isExist
    }
  },
  methods: {
    setWidth () {
      this.$refs.dropDown.style.width = this.width + 'px'
    },
    showDropDown () {
      this.isShow = !this.isShow
    }
  },
  mounted () {
    this.setWidth()
    // 点击区域外收回弹窗
    let $body = document.getElementsByTagName('html')[0]
    $body.onclick = e => {
      let isExist = false
      e.path.forEach(item => {
        if (item.className && item.className.includes('re-drop-menu')) {
          isExist = true
        }
      })
      isExist || (this.isShow = false)
    }
  }
}
</script>

<style lang="scss" scoped>
    .re-drop-menu {
        width: 120px;
        height: auto;
        text-align: center;

        .re-drop-link {
            font-size: 12px;
            color: #409eff;
            line-height: 16px;
            cursor: pointer;
        }

        .re-drop-down-list {
            width: auto;
            height: auto;
            margin-top: 8px;
            padding: 8px 0;
            border-radius: 3px;
            border: 1px solid #ebeef5;
            background-color: white;
            font-size: 14px;
            box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
            .drop-down-item {
                line-height: 24px;
                text-align: center;
                list-style: none;
                padding: 0 20px;
                margin: 0;
                font-size: 14px;
                color: #606266;
                cursor: pointer;
                outline: none;
                &:hover {
                    color: #409eff;
                    background-color: #ecf5ff;
                    cursor: pointer;
                }
            }
        }
    }
</style>



